}
.menu-item {
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  transition: all 1s ease;
  overflow: hidden;
  list-style: none;
  text-decoration: none; }
  .menu-item.larger {
    height: 380px; }
  .menu-item:first-child {
    margin-right: 7.5px; }
  .menu-item:last-child {
    margin-left: 7.5px; }
  .menu-item .background-image {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover; }
  .menu-item:hover {
    cursor: pointer; }
    .menu-item:hover .content {
      opacity: 0.9; }
    .menu-item:hover .background-image {
      transform: scale(1.1);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95); }
  .menu-item .content {
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: white;
    position: absolute;
    opacity: 0.4;
    color: black; }
    .menu-item .content .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a; }
    .menu-item .content .subtitle {
      font-weight: lighter;
      font-size: 16px; }

.directory-menu {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 80px; }

